<template>
  <div class="login_container">
    <div class="login_logo">
      <img src="../assets/logo.png" />
    </div>
    <div class="login_input">
      <div class="login_input_title">
        <span id="test">用户登录</span>
      </div>
      <div class="login_input_info">
        <van-form
          :model="loginForm"
          ref="loginFormRef"
          validate-trigger="onBlur"
          @submit="loginHandler"
          id="loginForm"
        >
          <van-field
            v-model="loginForm.username"
            name="username"
            label="用户名"
            placeholder="用户名"
            :rules="ruleForm.username"
            class="uname"
          />
          <van-field
            v-model="loginForm.password"
            type="password"
            name="password"
            label="密码"
            placeholder="密码"
            :rules="ruleForm.password"
            class="pwd"
          />
          <div class="login_btn">
            <van-button round type="info" native-type="submit" class="login_btn_submit">
              登录
            </van-button>
            <van-button
              type="danger"
              round
              class="login_btn_cancel"
              clearable="true"
              @click="resetForm"
              >重置</van-button
            >
          </div>
        </van-form>
      </div>
    </div>
    <div class="register_btn">
      <span @click="toRegister">立即注册</span>
    </div>
  </div>
</template>

<script>
import { Login } from '../api/user.js'
import { Toast } from 'vant'
export default {
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
      },
      ruleForm: {
        username: [{ validator: this.validator_uname, message: '用户名格式不合法' }],
        password: [{ validator: this.validator_pwd, message: '密码格式不合法' }],
      },
    }
  },
  methods: {
    validator_uname: function(val) {
      //字母开头，允许3-16字节，允许字母数字下划线
      return /^[a-zA-Z][a-zA-Z0-9_]{2,15}$/.test(val)
    },
    validator_pwd: function(val) {
      //以字母开头，长度在6~18之间，只能包含字母、数字和下划线
      return /^[a-zA-Z]\w{5,17}$/.test(val)
    },
    // onFailed(errorInfo) {
    //   console.log('failed', errorInfo)
    // },
    resetForm: function() {
      ;(this.loginForm.username = ''),
        (this.loginForm.password = ''),
        this.$refs.loginFormRef.resetValidation()
    },
    toRegister: function() {
      this.$router.push('/register')
    },
    login: function() {
      this.$refs.loginFormRef.validate(valid => {
        if (!valid) {
          return valid
        } else {
          this.loginHandler()
        }
      })
    },
    // loginValidae:function(form){
    //   // var result = serializeToJson($('#loginForm'))
    //   // console.log(result)
    //   var result = {}
    //   var f = form.serializeArray()
    //   f.forEach(function(item){
    //     result[item.name]=item.value;
    //   })
    // },
    loginHandler() {
      // var uname = document.querySelector('.uname')
      // console.log(uname.value)
      // console.log(this.loginForm.username)
      var obj = {}
      obj.username = this.loginForm.username
      obj.password = this.loginForm.password
      console.log(obj)

      // this.$axios.post('/login', obj).then((req, res) => {
      //   console.log(res)
      // })
      Login(obj)
        .then(res => {
          if (res.code == 200) {
            Toast.success(res.message)
          } else {
            Toast.fail(res.message)
          }
        })
        .catch(e => {
          console.log(e)
        })
    },
  },
}
</script>

<style scoped>
.login_container {
  position: relative;
  height: 100%;
}
.login_logo {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 300px;
  height: 130px;
}
.login_logo img {
  width: 100%;
  height: 100%;
  transition: all 0.3s;
}
.login_logo img:hover {
  transform: scale(1.1);
}
.login_input {
  position: absolute;
  top: 190px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
}
.login_input_title span {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 0 10px;
  font-size: 25px;
  margin-bottom: 10px;
}
.login_input_name {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 0 10px;
}
.login_input_pwd {
  position: absolute;
  top: 120px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  padding: 0 10px;
}
.login_btn_submit {
  margin: 0 20px;
}
.login_btn_cancel {
  margin: 0 20px;
}
.login_btn {
  position: absolute;
  top: 145px;
  left: 50%;
  transform: translateX(-50%);
  width: 400px;
  height: 50px;
}
.register_btn {
  position: absolute;
  top: 400px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}
</style>
